export default{
    rank:0,//0未登录，1普通用户，2会员，3超级管理员
    isAdmin:false,
    gLoading:true,
    isSigned:false,
    setIsAdmin(a){
        this.isAdmin = a;
    },
    setRank(a){
        this.rank = a;
        if(a ==3)this.setIsAdmin(true);
        else{this.setIsAdmin(false)}
    },
    isLoaded(){
        this.gLoading = false;
        console.log(this.gLoading)
    },
    setSignIn(){
        this.isSigned = true;
    },
    setSignOut(){
        this.isSigned = false;
    }
}


